@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans/opensans.woff2") format("woff2"), url("../fonts/opensans/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans/opensansbold.woff2") format("woff2"), url("../fonts/opensans/opensansbold.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: "Muller";
  src: url("../fonts/muller/mullerbold.woff2") format("woff2"), url("../fonts/muller/mullerbold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

html {
  box-sizing: border-box;
  min-width: 320px; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-family: "Open Sans", "Arial", "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 16px; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header__panel {
  padding: 8px 16px;
  min-height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(90deg, #3951e7 20.77%, #832ab9 91.64%); }

.hedaer__language-switcher {
  position: relative;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-right: 32px;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  text-decoration: none;
  color: #ffffff; }
  .hedaer__language-switcher::after {
    position: absolute;
    top: 0;
    right: 0;
    content: "";
    display: block;
    width: 24px;
    height: 24px;
    background-repeat: no-repeat;
    background-position: center; }
  .hedaer__language-switcher--en::after {
    background-image: url("../img/united-kingdom.svg"); }

.header__logo {
  width: 161px;
  height: 16px; }

.header__nav {
  position: relative;
  padding: 8px 16px;
  background-color: #ffffff; }

.header__title {
  position: relative;
  padding-left: 32px;
  font-family: "Muller", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 700; }
  .header__title::after {
    position: absolute;
    left: 0;
    top: -4px;
    content: "";
    width: 25px;
    height: 25px;
    background-image: url("../../img/logo_badge.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain; }

@media (min-width: 967px) {
  .header__panel {
    padding: 8px 16px; }
  .header__nav {
    padding: 16px 16px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  .header__title {
    font-size: 16px;
    line-height: 1.2px;
    padding-left: 38px;
    margin-right: 32px; }
    .header__title::after {
      left: 0;
      top: -16px;
      width: 30px;
      height: 30px; } }

@media (min-width: 1280px) {
  .header__panel {
    padding: 8px 40px; }
  .header__nav {
    padding: 16px 40px;
    align-items: center; }
  .header__title {
    font-size: 28px;
    line-height: 28px;
    padding-left: 66px;
    margin-right: 40px; }
    .header__title::after {
      left: 0;
      top: -16px;
      width: 50px;
      height: 50px; } }

@media (max-width: 967px) {
  .main-nav {
    position: absolute;
    right: 16px;
    left: 0;
    top: 8px; }
  .main-nav__toggle {
    position: absolute;
    top: 1px;
    right: 0;
    z-index: 11;
    display: block;
    width: 24px;
    height: 32px;
    cursor: pointer;
    border: none;
    background-color: transparent;
    padding: 0; }
    .main-nav__toggle:hover, .main-nav__toggle:active {
      background-color: #ffffff; }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #3951e7;
    box-shadow: 0 8px 0 0 #3951e7, 0 16px 0 0 #3951e7; }
  .main-nav--opened .main-nav__toggle {
    top: 1px;
    right: 0; }
    .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
      content: "";
      position: absolute;
      top: 16px;
      left: 0;
      width: 24px;
      height: 2px;
      background-color: #3951e7; }
    .main-nav--opened .main-nav__toggle::before {
      transform: rotate(45deg);
      box-shadow: none; }
    .main-nav--opened .main-nav__toggle::after {
      transform: rotate(-45deg); }
  .main-nav--closed .site-list__item {
    display: none; }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 10;
    min-height: 70vh;
    background-color: #ffffff; }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0; }
  .main-nav--nojs .site-list__item {
    display: block; } }

@media (min-width: 967px) {
  .main-nav__toggle {
    display: none; } }

@media (min-width: 1280px) {
  .main-nav {
    flex-grow: 1;
    max-width: 773px; } }

.site-list {
  list-style: none;
  margin: 0; }

@media (max-width: 967px) {
  .site-list {
    max-width: 95%;
    list-style: none;
    padding: 18px;
    margin: 0; }
  .site-list__item {
    margin-bottom: 8px; }
  .site-list__link {
    display: block;
    padding: 4px 0;
    text-decoration: none;
    color: #222222;
    border-bottom: 1px solid #3951e7; } }

@media (min-width: 967px) {
  .site-list {
    padding: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  .site-list__item:not(:last-child) {
    margin-right: 8px; }
  .site-list__link {
    font-size: 14px;
    color: #222222;
    text-decoration: none; } }

@media (min-width: 1280px) {
  .site-list__item:not(:last-child) {
    margin-right: 16px; }
  .site-list__link {
    font-size: 16px;
    line-height: 24px; } }
